body {
  background: #86b584 radial-gradient(circle, #a0d79e 0%, #046100 79%);
  width: 100vw;
  height: 100vh;
}

.header {
  justify-content: center;
  width: 100vw;
  font-family: Casino;
  display: flex;
}

.header__h1 {
  background: #dceb0c linear-gradient(to top, #ef0 0%, #ff6a00 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  width: 235px;
  margin: 30px 0;
  font-size: 50px;
}

.main {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80%;
  display: flex;
}

.main__bet {
  background-color: #000000b1;
  border: 2px solid #fff;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 80%;
  height: 50%;
  transition: all .2s;
  display: flex;
  position: absolute;
  transform: scale(0);
  box-shadow: inset 5px 5px 10px #fff, inset -5px -5px 10px #fff;
}

@media (width >= 1000px) {
  .main__bet {
    max-width: 1000px;
  }
}

.main__bet--betButtons {
  grid-template-rows: 50px 50px 50px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 2px;
  display: grid;
}

.main__bet--betButtons > * {
  color: #fff;
  background: none;
  border: 2px solid #fff;
  padding: 0;
  font-size: 20px;
  transition: all .2s;
}

.main__bet--amount {
  color: #fff;
  text-align: center;
  background: none;
  border: 2px solid #fff;
  border-radius: 10px;
  width: 60%;
  padding: 10px;
  font-size: 30px;
  font-weight: bold;
}

.main__bet--apply {
  color: #fff;
  background: none;
  border: 2px solid #fff;
  border-radius: 10px;
  width: 30%;
  padding: 10px;
  font-size: 30px;
  font-weight: bold;
  transition: all .2s;
}

.main__bet--apply:focus, .main__bet--betButtons > :focus {
  color: #000;
  background-color: #fff;
  border: 2px solid #000;
}

.main__buttons {
  box-sizing: border-box;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 200px;
  margin-top: 30px;
  display: flex;
  position: relative;
}

@media (width >= 700px) {
  .main__buttons {
    max-width: 60%;
  }
}

.main__buttons--cash {
  color: #fff;
  font-family: Casino;
  font-size: 50px;
  transition: all .2s;
  position: absolute;
  top: -55px;
  transform: scale(0);
}

.main__buttons--hit:focus, .main__buttons--pass:focus {
  background: #97cc95 radial-gradient(circle, #55ad52 0%, #046100 79%);
}

.main__buttons--hit, .main__buttons--pass {
  opacity: .4;
  background: #86b584 radial-gradient(circle, #489446 0%, #034d00 79%);
  border: 5px solid #023c00;
  border-radius: 16px;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 80px;
  padding: 10px;
  font-family: Casino;
  transition: all .2s;
  display: flex;
  transform: scale(0);
}

.main__buttons--hit-p, .main__buttons--pass-p {
  background: #dceb0c linear-gradient(to top, #ef0 0%, #ff6a00 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 43px;
}

.main__table {
  box-sizing: border-box;
  background-image: url("table.1ff71fbb.svg");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 600px;
  display: flex;
  position: relative;
}

.main__table--startButton {
  background: #dceb0c linear-gradient(to top, #ef0 0%, #ff6a00 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: none;
  font-family: Casino;
  font-size: 80px;
  transition: all .2s;
  position: absolute;
  top: calc(50% - 50px);
  left: calc(50% - 112px);
}

.table__botSide, .table__playerSide {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 99%;
  height: 135px;
  padding: 0;
  transition: all .3s;
  display: flex;
}

.cardsFoldingClass {
  grid-template-columns: 30px 30px 30px 30px 30px;
  display: grid;
}

.card {
  width: 80px;
  height: 110px;
  transition: all .2s;
  left: 10%;
}

.cardHoldersActivated {
  border: 5px solid #000;
  box-shadow: inset 5px 5px 15px #000, inset -5px -5px 15px #000;
}

.loseScreen, .winScreen, .tieScreen, .blackjackEvent {
  width: 100vw;
  height: 100vh;
  font-size: 50px;
  transition: all .2s;
  position: absolute;
  top: 0;
  transform: scale(0);
}

.loseScreen__background, .winScreen__background, .tieScreen__background, .blackjackEvent__background {
  background-color: #0000009b;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
}

.loseScreen__text, .winScreen__text, .tieScreen__text, .blackjackEvent__text {
  background: #dceb0c linear-gradient(to top, #ef0 0%, #ff6a00 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Casino;
}

.rotated {
  animation: .2s linear revertCardRotate;
}

.firstRotate {
  animation: .2s linear firstRotate;
}

.cardPopIn {
  animation: .4s linear cardPopIn;
}

@keyframes cardPopIn {
  from {
    transform: translateX(1000px);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes revertCardRotate {
  from {
    transform: rotate3d(0, 1, 0, 90deg);
  }

  to {
    transform: rotate3d(0, 1, 0, 0);
  }
}

@keyframes firstRotate {
  from {
    transform: rotate3d(0, 1, 0, 0);
  }

  to {
    transform: rotate3d(0, 1, 0, 90deg);
  }
}

